.swiper-container-flip .swiper-slide-shadow-right,
.swiper-container-flip .swiper-slide-shadow-top {
  z-index: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

html {
  box-sizing: border-box;
  font-size: 14px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

nav,
footer,
header,
aside {
  display: block;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active,
a:focus {
  outline: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  vertical-align: top;
}

body {
  width: 100%;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.5px;
  background-color: var(--color-smoky-white);
}

._container {
  max-width: 980px;
  padding: 0 20px;
  margin: 0 auto;
}

._top-bg {
  height: 151px;
  background-color: var(--color-white-aluminum);
  background-image: url(../img/geometric-leaves2.png);
  background-position: center;
}

._head-layout {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 940px;
  height: 130px;
  padding-left: 38px;
  margin-top: -28px;
  background-image: url(../img/container.jpg);
}

._layout {
  position: relative;
  z-index: 10;
  max-width: 940px;
  padding: 31px 27px 27px;
  margin-bottom: -31px;
  background-color: var(--color-white);
  border: 1px solid var(--color-gainsborough);
  border-bottom: none;
}

._layout-title,
._layout-text {
  font-family: 'Bitter', serif;
  font-weight: 700;
}

._layout-title {
  margin-bottom: 5px;
  font-size: 30px;
  color: var(--color-white);
}

._layout-text {
  font-size: 14px;
  color: var(--color-gainsborough);
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

._btn-reset {
  cursor: pointer;
  background: transparent;
  border: none;
}

.header {
  position: relative;
  z-index: 30;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.5);
}

.header__top {
  height: 47px;
  background-color: var(--color-black-brown);
  background-image: url(../img/top-bar.jpg);
}

.header__top._active {
  display: block;
  position: fixed;
  left: 0;
  right: 0;
  background-image: none;
  background-color: transparent;
  z-index: 20;
}

.header__top._active .header__top-container {
  justify-content: center;
  margin-top: 7px;
}

.header__top-container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
}

.header__social-inner {
  display: flex;
  gap: 8px;
}

.header__social-icon {
  fill: var(--color-white);
  transition: fill 0.2s;
}

.header__social-link {
  width: 25.5px;
  height: 47px;
  line-height: 51px;
  text-align: center;
}

.header__bottom {
  background-color: var(--color-white);
}

.header__bottom-container {
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  max-width: 120px;
  font-family: 'Bitter', serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 56px;
  cursor: pointer;
}

.header__logo-text {
  color: var(--color-black-brown);
}

.header__logo-i {
  padding: 3px 13.34px;
  margin-right: 9px;
  color: #fff;
  text-align: center;
  background-color: var(--color-mandarin-tango);
}

.header__logo-dot {
  color: var(--color-mandarin-tango);
}

.menu-header {
  display: flex;
}

.menu-header__burger {
  display: none;
}

.menu-header__burger._active span {
  transform: scale(0);
}

.menu-header__burger._active span:first-child {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}

.menu-header__burger._active span:last-child {
  bottom: calc(50% - 1px);
  transform: rotate(45deg);
}

.nav__list {
  display: flex;
  gap: 27px;
}

.nav__link {
  display: block;
  padding-top: 2px;
  font-size: 12px;
  font-weight: 700;
  line-height: 54px;
  color: var(--color-black-brown);
  text-transform: uppercase;
  transition: opacity 0.2s;
}

.nav__link--active {
  color: var(--color-mandarin-tango);
}

.nav._active .nav__list {
  margin-top: 27px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.nav._active .nav__link {
  line-height: 47px;
  color: var(--color-smoky-white);
  text-shadow: 2px 2px 2px black;
}

.nav._active .nav__link--active {
  color: var(--color-mandarin-tango);
  text-shadow: 2px 2px 2px black;
}

.home__slider {
  height: calc(100vh - 103px);
  overflow: hidden;
}

.home__slide {
  position: relative;
  width: 100%;
  background-color: var(--color-white-aluminum);
  background-position: center;
  background-size: cover;
}

.home__slide-1 {
  background-image: url(../img/slide-1.jpg);
}

.home__slide-2 {
  background-image: url(../img/slide-2.jpg);
}

.home__slide-3 {
  background-image: url(../img/slide-3.jpg);
}

.home__slide-4 {
  background-image: url(../img/slide-4.jpg);
}

.home__slide-5 {
  background-image: url(../img/slide-5.jpg);
}

.home__slide-6 {
  background-image: url(../img/slide-6.jpg);
}

.home__slide-7 {
  background-image: url(../img/slide-7.jpg);
}

.home__slide-8 {
  background-image: url(../img/slide-8.jpg);
}

.home__slide-9 {
  background-image: url(../img/slide-9.jpg);
}

.home__slide-10 {
  background-image: url(../img/slide-10.jpg);
}

.home__slide-inner {
  position: relative;
  top: 19%;
  left: 20px;
  padding: 0;
}

.home__slide-title,
.home__slide-desc {
  display: inline-block;
  padding: 8px 15px;
  margin: 1px;
  font-family: 'Bitter', serif;
  font-size: 30px;
  font-weight: 700;
  color: var(--color-white);
  background-color: rgba(0, 0, 0, 0.8);
}

.home__slide-btn--inner {
  position: absolute;
  top: 19%;
  z-index: 11;
  width: 100%;
  margin-top: 260px;
}

.home__slide-btn {
  padding: 14px 17px 10px;
  margin-right: 1px;
  background-color: var(--color-mandarin-tango);
  transition: opacity 0.3s;
}

.home__slider-thumbs {
  position: relative;
  z-index: 12;
  max-width: 940px;
  height: 130px;
  margin-top: -130px;
  background-color: rgba(0, 0, 0, 0.2);
  background-image: none;
  transition: margin-top 0.3s;
}

.home__thumbs-wrapper {
  align-items: center;
  justify-content: space-between;
  cursor: default;
}

.home__thumb {
  box-sizing: border-box;
  max-width: 75px;
  height: 75px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.6;
  transition: opacity 0.3s;
}

.home__thumb:hover {
  opacity: 1;
}

.home__thumb:nth-child(1) {
  background-image: url(../img/slide-sm-1.jpg);
}

.home__thumb:nth-child(2) {
  background-image: url(../img/slide-sm-2.jpg);
}

.home__thumb:nth-child(3) {
  background-image: url(../img/slide-sm-3.jpg);
}

.home__thumb:nth-child(4) {
  background-image: url(../img/slide-sm-4.jpg);
}

.home__thumb:nth-child(5) {
  background-image: url(../img/slide-sm-5.jpg);
}

.home__thumb:nth-child(6) {
  background-image: url(../img/slide-sm-6.jpg);
}

.home__thumb:nth-child(7) {
  background-image: url(../img/slide-sm-7.jpg);
}

.home__thumb:nth-child(8) {
  background-image: url(../img/slide-sm-8.jpg);
}

.home__thumb:nth-child(9) {
  background-image: url(../img/slide-sm-9.jpg);
}

.home__thumb:nth-child(10) {
  background-image: url(../img/slide-sm-10.jpg);
}

.home__thumb.swiper-slide-thumb-active {
  border: 2px solid var(--color-mandarin-tango);
  opacity: 1;
}

.home__slider-pags-inner {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
  display: flex;
  justify-content: center;
  padding: 10px;
  margin-bottom: 20px;
}

.home__slider-pagination {
  z-index: 10;
  display: flex;
  gap: 10px;
}

.home__slider-pagination .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
}

.features {
  display: flex;
  gap: 39px;
  padding-top: 17px;
}

.features__col {
  width: 33%;
}

.features__icon {
  display: inline-block;
  width: 31px;
  height: 31px;
  margin-right: 13px;
  line-height: 31px;
  text-align: center;
  background-color: var(--color-black-brown);
}

.features__icon img {
  vertical-align: middle;
}

.features__title {
  display: inline-block;
  font-family: 'Bitter', serif;
  font-size: 18px;
  font-weight: 500;
  vertical-align: middle;
}

.features__text {
  margin-top: 17px;
  line-height: 17px;
  color: var(--color-wet-asphalt);
}

.features__bottom {
  padding-bottom: 8px;
  padding-bottom: 36px;
  margin-top: 55px;
  margin-bottom: 19px;
  font-family: 'Bitter', serif;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
}

.features__bottom-span {
  padding: 8px 13px;
  margin: 0 6px;
  font-weight: 700;
  color: #fff;
  background-color: var(--color-mandarin-tango);
}

.projects {
  padding: 0;
}

.projects__filter {
  display: flex;
  flex-wrap: wrap;
  padding-right: 10px;
  margin-bottom: 27px;
}

.projects__btn {
  padding: 9px 10px 7px;
  margin-bottom: 5px;
  font-family: 'Bitter', serif;
  font-size: 15px;
  font-weight: 500;
  color: var(--color-black-brown);
  transition: opacity 0.2s;
}

.projects__btn:hover {
  opacity: 0.6;
}

.projects__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  overflow: hidden;
}

.projects__item {
  position: relative;
  width: 100%;
  height: 120px;
  overflow: hidden;
  cursor: pointer;
  background-color: var(--color-white-aluminum);
}

.projects__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.projects__overlay {
  position: absolute;
  right: 0;
  bottom: -30px;
  left: 0;
  padding: 7px 10px;
  font-family: 'Bitter', serif;
  font-weight: 700;
  color: var(--color-white);
  background-color: rgba(0, 0, 0, 0.6);
  transition: transform 0.3s;
}

.projects__hide-work {
  display: none;
}

.projects__btn--active {
  font-weight: 500;
  color: var(--color-white);
  background-color: var(--color-mandarin-tango);
}

.blog__layout {
  display: flex;
  justify-content: space-between;
  padding-bottom: 31px;
}

.blog__list {
  max-width: 650px;
  width: 100%;
  padding-right: 15px;
}

.blog__aside-list {
  flex: 0 0 206px;
  margin-left: 15px;
}

.blog__aside-item {
  margin-bottom: 15px;
}

.blog__aside-item:last-child {
  margin-bottom: 0;
}

.blog__list-item {
  margin-bottom: 30px;
  border-bottom: 5px solid var(--color-mandarin-tango);
}

.blog__list-item:last-of-type {
  border-bottom: none;
}

.blog__article {
  padding-bottom: 31px;
}

.blog__content-link {
  padding: 5.7px 6.3px 5.2px 6.7px;
  font-weight: 700;
  color: var(--color-black-brown);
  transition: opacity 0.2s;
}

.blog__content-link--active {
  color: var(--color-white);
  background-color: var(--color-mandarin-tango);
}

.blog-article__title {
  margin-bottom: 15px;
  font-family: 'Bitter', serif;
  font-size: 18px;
  color: var(--color-gray-brown);
}

.blog-article__audio {
  width: 100%;
}

.blog-article__image {
  display: block;
  margin: 0 auto;
}

.blog-article__inner {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  gap: 15px;
  color: var(--color-black-brown);
}

.blog-article__description {
  width: 100%;
  max-width: 105px;
}

.blog-article__desc-item {
  font-weight: 500;
  font-family: 'Bitter', serif;
}

.blog-article__desc-item:not(:first-child) {
  margin-top: 15px;
}

.blog-article__desc-value {
