}

.blog-article__video-inner {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56.25%;
}

.blog-article__video {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.aside-article {
  padding-bottom: 9px;
}

.aside-article__title {
  padding-bottom: 6px;
  margin-bottom: 20px;
  font-family: 'Bitter', serif;
  font-size: 16px;
  color: var(--color-black-brown);
  border-bottom: 5px solid var(--color-mandarin-tango);
}

.aside-article__text {
  line-height: 18px;
  color: var(--color-black-brown);
}

.aside-article__list-item {
  margin-bottom: 3px;
}

.aside-article__item-link {
  color: var(--color-black-brown);
  transition: opacity 0.2s;
}

.aside-article__adv-inner {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.aside-article__adv {
  width: 100px;
  height: 100px;
}

.footer {
  background-color: var(--color-black-brown);
  background-image: url(../img/footer-container.jpg);
  border-top: 8px solid var(--color-mandarin-tango);
}

.footer__container {
  display: flex;
  gap: 26px;
  padding-right: 20px;
  padding-bottom: 46px;
  padding-left: 20px;
  margin-top: 61px;
}

.footer__col {
  width: 300px;
}

.footer__title {
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 700;
  color: var(--color-white);
  text-transform: uppercase;
}

.footer__posts-text {
  padding-right: 6px;
  margin-top: 15px;
  font-family: 'Bitter', serif;
  font-weight: 700;
  line-height: 18px;
  color: var(--color-white-aluminum);
  letter-spacing: 0.5px;
}

.footer__posts-text:not(:last-child) {
  padding-bottom: 15px;
  border-bottom: 1px solid var(--color-gray-brown);
}

.footer__about-text {
  font-weight: 700;
  line-height: 18px;
  color: var(--color-white-aluminum);
}

.footer__about-text:not(:last-child) {
  margin-bottom: 18px;
}

.footer__social-icons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.footer__social-link {
  transition: transform 0.2s;
}

.footer__copyright {
  margin-top: 30px;
  color: var(--color-white-aluminum);
}

@media (min-width: 480px) {
  .projects__item:hover .projects__overlay {
