  }

  .footer {
    border-top-width: 4px;
  }

  .footer__container {
    margin-top: 40px;
  }
}

@media (hover: hover) {
  .header__social-link:hover .header__social-icon {
    fill: var(--color-mandarin-tango);
  }

  .nav__link:hover {
    opacity: 0.6;
  }

  .home__slide-btn:hover {
    opacity: 0.6;
  }

  .blog__content-link:hover {
    opacity: 0.6;
  }

  .aside-article__item-link:hover {
